.screen {
    background-color: rgba(0, 0, 0, 0.6);
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    z-index: 998;
}

.modal-container {
    background-color: $clr5;
    width: 50%;
    margin: 0 auto;
    z-index: 999;
    padding: 20px;
    color: $clr1;
    position: fixed;
    left: 25%;

    .title {
        font-family: rubikBold;
        border-bottom: 1px solid #dbdbdb;
        padding-bottom: 5px;
        margin-bottom: 10px;
        width: 100%;
    }

    .content {
        margin-bottom: 10px;
    }

    .btn {
        margin-left: 15px;
    }
}
