.nav-container {
    background-color: #f9f9f9;
    width: 100%;
    height: 71px;
    box-shadow: 0px 0px 10px 0px rgba(15, 38, 26, 1);
    position: fixed;
    top: 30px;
    z-index: 99;
    transition: 0.1s;

    .mobile-greet {
        display: none;
    }

    .screen {
        display: none;
    }

    .menu-btn {
        display: none;
    }

    .close-menu {
        display: none;
    }

    &.scrolled {
        top: 0;
    }

    .greet {
        color: $clr1;

        a {
            font-family: rubikBold;
            margin-right: 3px;

            &:hover {
                text-decoration: underline;
            }
        }

        .logout {
            font-size: rem(11px);
            color: #8d8d8d;
            margin-right: 3px;
            cursor: pointer;
        }
    }

    .main-nav {
        & li a {
            height: 71px;
            padding: 0 25px;
            display: flex;
            align-items: center;
            color: #648db7;
            &:hover {
                color: $clr4;
            }

            &:not(:last-of-type) {
                margin-left: 10px;
            }
        }
        .active {
            height: 71px;
            padding: 0 20px;
            display: flex;
            align-items: center;
            border-bottom: 5px solid $clr4;
            padding-top: 5px;
            color: $clr4;
        }
    }
}

@media (max-width: 1160px) {
    .nav-container {
        padding: 0 20px;
    }
}

@media (max-width: 960px) {
    .nav-container {
        line-height: 71px;

        .screen {
            display: block;
        }

        .close-menu {
            background: url('../img/wrong.png') no-repeat;
            width: 24px;
            height: 24px;
            display: block;
            border: none;
            border-right: 1px solid rgb(196, 196, 196);
            padding-right: 38px;
        }

        .menu-btn {
            display: block;
            background: url("../img/menu.png");
            border: none;
            width: 36px;
            height: 36px;
        }

        .main-nav {
            background-color: $clr5;
            flex-direction: column;
            position: fixed;
            height: 100vh;
            width: 220px;
            right: 0;
            top: 0;
            transform: translateX(105%);
            transition: 0.3s;
            z-index: 999;

            li {
                .active {
                    background-color: #eeeeee;
                }
            }

            &.menu-open {
                transform: translateX(0);
            }

            &.menu-open ~ .screen {
                visibility: visible;
                opacity: 1;
            }
        }

        .screen {
            right: 0;
            visibility: hidden;
            opacity: 0;
            transition: 0.3s;
            cursor: pointer;
        }
    }
}

@media (max-width: 600px) {
    .nav-container {
        .greet {
            display: none;
        }

        .mobile-greet {
            display: block;
            color: $clr1;
            padding: 0 0;
            font-size: rem(13px);

            small {
                cursor: pointer;
            }
        }
    }
}

.logo-container {
    .logo-icon {
        width: 48px;
        height: 48px;
        margin-right: 10px;
    }

    .logo {
        font-size: rem(32px);
        color: $clr4;
        font-family: openSansBold;
    }
}

@media (max-width: 500px) {
    .logo-container {
        .logo-icon {
            width: 38px;
            height: 38px;
        }

        .logo {
            font-size: rem(26px);
        }
    }
}
