.btn {
    padding: 11px 20px;
    border: none;
    border-radius: 5px;
    font-family: rubikBold;
    background-color: $clr5;

    &:active,
    &:hover:not(:disabled) {
        background-color: #eeeeee;
    }

    &.primary {
        background-color: $clr4;
        transition: 0.3s;
        color: $clr5;

        &:hover {
            background-color: darken($clr4, 7);
        }
    }

    &.danger {
        background-color: #c0392b;
        transition: 0.3s;
        color: $clr5;
        
        &:hover {
            background-color: darken($color: #c0392b, $amount: 7);
        }
    }
}
