.news-letter-container {
    background: -webkit-linear-gradient(left, #3867d6 0%, #5489f9 100%);
    width: 70%;
    padding: 20px;
    margin: 40px auto;
    border-radius: 15px;
    min-height: 90px;

    .msg {
        font-size: rem(13px);
        color: $clr2;
        font-family: rubik;
    }

    .title {
        text-align: center;
        font-family: rubikBold;
        text-shadow: 0 1px 2px #0f265a;
    }

    .news-letter-form {
        width: 50%;

        .input {
            border-radius: 5px;
            border: none;
            margin-left: 15px;
            width: 70%;
        }

        .btn {
            min-width: 100px;
            color: $clr4;
        }
    }
}

@media (max-width: 1060px) {
    .news-letter-container {
        .title {
            font-size: rem(20px);
        }
    }
}

@media (max-width: 860px) {
    .news-letter-container {
        width: 89%;
    }
}

@media (max-width: 760px) {
    .news-letter-container {
        flex-direction: column;
        min-height: 120px;

        .title {
            margin-bottom: 10px;
        }

        .news-letter-form {
            width: 90%;
        }
    }
}

@media (max-width: 460px) {
    .news-letter-container {
        min-height: 160px;
        margin: 20px auto;

        .title {
            font-size: rem(17px);
        }

        .news-letter-form {
            flex-direction: column;
            align-items: center;

            .input {
                margin-bottom: 10px;
                width: 90%;
                margin-left: 0;
            }
        }
    }
}
