.hero-container {
    padding-top: 55px;
    margin-bottom: 13px;
    margin-top: 72px;

    .cta-container {
        padding-right: 40px;

        .header-main-title {
            background-image: linear-gradient(to bottom, #ffffff 0%, #c0c0c0 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            font-size: rem(56px);
            font-family: rubikBold;
            filter: drop-shadow(3px 2px rgba(13, 41, 74, 1));
            margin-bottom: 20px;
        }

        .cta-btn {
            background: linear-gradient(to bottom, #ffe200 0%, #ffc000 100%);
            padding: 12px 26px;
            font-size: rem(30px);
            color: white;
            font-family: rubikBold;
            border-radius: 10px;
            text-shadow: 0px 3px 2px #e9b005;
            margin-right: 155px;
        }
    }
    .img-container {
        max-width: 320px;
    }
}

@media (max-width: 1100px) {
    .hero-container {
        .cta-container {
            flex: 1;
            .header-main-title {
                font-size: rem(40px);
            }

            .cta-btn {
                margin-right: 0;
                font-size: rem(24px);
            }
        }
    }
}

@media (max-width: 900px) {
    .hero-container {
        .cta-container {
            align-items: center;
            .header-main-title {
                font-size: rem(50px);
                text-align: center;
            }

            .cta-btn {
                font-size: rem(20px);
            }
        }
    }
}

@media (max-width: 760px) {
    .hero-container {
        .cta-container {
            .header-main-title {
                font-size: rem(36px);
            }

            .cta-btn {
                font-size: rem(16px);
            }
        }
    }
}

@media (max-width: 600px) {
    .hero-container {
        margin-top: 30px;
        flex-direction: column;
        align-items: center;

        .cta-container {
            margin-bottom: 20px;
            padding-right: 0;
        }

        .img-container {
            max-width: 220px;
        }
    }
}

@media (max-width: 486px) {
    .hero-container {
        margin-bottom: 0;
    }
}

.test-now-container {
    background-color: #eeeeee;
    padding: 10px;
    width: 100%;

    .title {
        font-family: rubikLight;
        background-color: $clr1;
        padding: 10px 15px;
        margin-left: 40px;
    }

    .test-options {
        & * {
            margin-left: 75px;
        }

        div {
            cursor: pointer;
        }

        .option {
            width: 48px;
            height: 30px;
        }

        .car {
            background: url(../img/car.png) no-repeat;
            background-size: contain;

            &:hover {
                background: url(../img/car-yellow.png) no-repeat;
                background-size: contain;
            }
        }

        .motorcycle {
            background: url(../img/motorcycle.png) no-repeat;
            background-size: contain;

            &:hover {
                background: url(../img/motorcycle-yellow.png) no-repeat;
                background-size: contain;
            }
        }

        .tractor {
            background: url(../img/tractor.png) no-repeat;
            background-size: contain;

            &:hover {
                background: url(../img/tractor-yellow.png) no-repeat;
                background-size: contain;
            }
        }

        .truck {
            background: url(../img/truck.png) no-repeat;
            background-size: contain;

            &:hover {
                background: url(../img/truck-yellow.png) no-repeat;
                background-size: contain;
            }
        }

        .bus {
            background: url(../img/bus.png) no-repeat;
            background-size: contain;

            &:hover {
                background: url(../img/bus-yellow.png) no-repeat;
                background-size: contain;
            }
        }
    }
}

@media (max-width: 850px) {
    .test-now-container {
        .title {
            min-width: 160px;
        }

        .test-options {
            & * {
                margin-left: 35px;
            }

            .option {
                min-width: 48px;
            }
        }
    }
}

@media (max-width: 620px) {
    .test-now-container {
        .test-options {
            overflow-x: scroll;
            max-width: 65%;

            &::-webkit-scrollbar {
                display: none;
            }

            & * {
                margin-left: 20px;
            }
        }
    }
}

.test-txt-container {
    width: 760px;
    margin: 0 auto;
}

@media (max-width: 1160px) {
    .test-txt-container {
        display: none;
    }
}

.txt {
    top: 12px;
    position: relative;
    transition: 0.3s;
    margin-top: -10px;
    color: $clr3;

    &.car-txt {
        margin-left: 38px;
    }

    &.motorcycle-txt {
        margin-left: 21px;
    }

    &.tractor-txt {
        margin-left: 8px;
    }

    &.truck-txt {
        margin-left: 16px;
    }

    &::before {
        content: "";
        background: url("../img/arrow.png") no-repeat;
        width: 19px;
        height: 12px;
        display: block;
        position: relative;
        right: 35px;
        top: -2px;
    }
}

.remove-txt {
    visibility: hidden;
    opacity: 0;
}

.show-txt {
    visibility: visible;
    opacity: 1;
}
