.login-signup-container {
    flex: 1;
    width: 100%;
    padding: 20px;
    margin-top: 72px;

    .signup-now {
        color: $clr5;
        font-size: rem(18px);
        display: block;
        margin-bottom: 20px;
        width: fit-content;

        &:hover {
            text-decoration: underline;
        }
    }

    .title {
        font-family: rubikBold;
    }

    .login-signup-form {
        margin-bottom: 20px;
        .input {
            margin-bottom: 10px;
            border: none;
            width: 270px;

            &:last-of-type {
                margin-bottom: 20px;
            }
        }
    }

    .msg {
        background-color: #c0392b;
        padding: 10px;
        font-size: rem(14px);
        font-family: rubikBold;
    }
}
