/* List Helpers */

.clean-list {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

/* Flex Helpers */

.flex {
    display: flex;
    &.align-center {
        align-items: center;
    }
    &.align-baseline {
        align-items: baseline;
    }
    &.column {
        flex-direction: column;
    }
    &.justify-center {
        justify-content: center;
    }
    &.justify-start {
        justify-content: flex-start;
    }
    &.space-between {
        justify-content: space-between;
    }
    &.space-evenly {
        justify-content: space-evenly;
    }
    &.space-around {
        justify-content: space-around;
    }
    &.wrap {
        flex-wrap: wrap;
    }
    &.align-start {
        align-items: flex-start;
    }
    &.align-baseline {
        align-items: baseline;
    }
    &.align-end {
        align-items: flex-end;
    }
    &.even {
        justify-content: space-evenly;
    }
    &.justify-end {
        justify-content: flex-end;
    }
}

.bold {
    font-family: rubikBold;
}

.grid {
    display: grid;
}

.pointer {
    cursor: pointer;
}

.input {
    padding: 11px;
    background-color: $clr5;
    transition: 0.3s;
    color: $clr1;

    &:focus {
        background-color: #eeeeee;
    }
}

.page-title {
    font-family: rubikBold;
    background-color: $clr5;
    padding: 10px 15px;
    color: $clr1;
    margin-bottom: 20px;
}

.no-display {
    visibility: hidden !important;
    display: none !important;
}
