* {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: rubik, Arial, sans-serif;
    color: $clr5;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    font-weight: normal;
}

img {
    width: 100%;
    object-fit: cover;
}

button {
    cursor: pointer;
}

textarea,
input,
button {
    font-family: rubik, Arial, sans-serif;
    outline: none;
}

p {
    margin: 0;
}

.main-content {
    background: url('../img/header-bg.jpg') no-repeat;
    width: 100%;
    height: 427px;
    background-size: cover;
    padding-top: 39px;

    &.page {
        background: $clr1;
        height: unset;
        min-height: 100vh;
    }
}