.main-footer {
    background-color: #f5f5f5;
    padding: 35px;
    color: $clr1;
    width: 100%;

    .footer-container {
        flex-direction: row-reverse;
    }

    .footer-logo-container {
        &.logo-container {
            padding-bottom: 10px;
            margin-bottom: 20px;
            filter: grayscale(100%);
            color: #7c7c7c;
            transition: 0.3s;
            margin-right: 100px;

            a {
                max-width: fit-content;
            }

            .logo {
                font-size: rem(36px);
            }

            .logo-icon {
                width: 65px;
                height: 65px;
            }

            &:hover {
                filter: grayscale(0%);
            }
        }
    }

    .content-container {
        .content {
            .title {
                font-family: rubikBold;
                margin-bottom: 7px;
                color: #44bd32;
                border-bottom: 3px solid #44bd32;
                width: 100%;
                padding-bottom: 7px;
                min-width: 180px;
                padding-right: 10px;
            }

            &:not(:first-child) {
                margin-right: 40px;
            }

            p {
                max-width: 325px;
                padding: 3px 10px;

                a {
                    text-decoration: underline;
                }
            }

            ul {
                padding: 3px 10px;
            }

            &:nth-of-type(2) {
                .title {
                    color: $clr3;
                    border-bottom: 3px solid $clr3;
                }
            }

            &:nth-of-type(3) {
                .title {
                    color: #e84118;
                    border-bottom: 3px solid #e84118;
                }
            }

            a {
                &:hover {
                    color: $clr4;
                }
            }
        }
    }
}

@media (max-width: 1100px) {
    .footer-container {
        .footer-logo-container {
            &.logo-container {
                margin-right: 30px;

                .logo {
                    font-size: rem(26px);
                }

                .logo-icon {
                    width: 38px;
                    height: 38px;
                }
            }
        }
    }
}

@media (max-width: 980px) {
    .main-footer {
        .footer-container {
            flex-direction: column;
        }
    }
}

@media (max-width: 760px) {
    .main-footer {
        .footer-container {
            .content-container {
                flex-direction: column;
                align-items: center;

                .content {
                    max-width: 80%;
                    width: 80%;
                    margin-bottom: 10px;

                    &:not(:first-child) {
                        margin-right: 0;
                    }

                    p {
                        max-width: 100%;
                    }
                }
            }
        }
    }
}

@media (max-width: 480px) {
    .main-footer {
        .footer-container {
            .content-container {
                .content {
                    max-width: 100%;
                    width: 100%;
                }
            }
        }
    }
}